* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

.button {
  border: 1px solid black;
  background-color: white;
  font-size: 20px;
  width: 70px;
  height: 70px;
}

ul {
  padding-left: 0;
}

.moves-list {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
}

.moves-item {
  margin: 10px;
}

.status{
  text-align: center;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
  font-size: 32px;
}

.square {
  background: #fff;
  border: 2px solid #999;
  float: left;
  font-size: 52px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-info {
  margin-top: 20px;
}
